.contenedor-anuncios{
    @include tablet{
        @include grid(3, 2rem);
    }

}
.anuncio{
    background-color: lighten($gris, 5);
    border: 1px solid darken($gris,10);
    margin-bottom: 2rem;
    .contenido-anuncio{
        padding: 2rem;
    }
    h3,p{
        margin: 0;
    }
}
.precio{
    color: $verde;
    font-weight: $bold;
    font-size: 3rem;
}
.iconos-caracteristicas{
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;
    li{
        flex: 1;
        display: flex;
        img{
            margin-right: 1rem;
            justify-content: space-between;
        }
        p{
            margin-left: 1rem;
            font-weight: $black;
        }
    }
}